import React from 'react';

import { Redirect } from "react-router-dom"
import Markdown from "react-markdown"
import postlist from "../../posts.json"

const BlogSingle = (props) => {
    console.log(props)
    

    let validid = props.match.params.url;
    console.log(validid)
    const fetchedPost = {}
    let postExists = false
    postlist.forEach((post, i) => {
        if (validid = post.url) {
            fetchedPost.title = post.title ? post.title : "No title given"
            fetchedPost.date = post.date ? post.date : "No date given"
            fetchedPost.author = post.author ? post.author : "No author given"
            fetchedPost.url = post.url ? post.url : "No url given"
            fetchedPost.imglink = post.imglink ? post.imglink : "No imglink given"
            fetchedPost.content = post.content ? post.content : "No content given"
            postExists = true
        }
    })
    if (postExists === false) {
        return <Redirect to="/404" />
    }
    return (
        <section className="blog_area sec_pad">
            <div className="container">
                <div className="row">
                    <div className="col-lg-8 blog_sidebar_left">
                        <div className="blog_single mb_50">
                        <img className="img-fluid" src={require(`../../img/${fetchedPost.imglink}`).default} alt=""/>
                            <div className="blog_content">

                                <small >Published on {fetchedPost.date} by {fetchedPost.author}</small>
                                <h5 className="f_p f_size_30 f_500 t_color mb_20">{fetchedPost.title}</h5>
                                <div>  
                                    <Markdown children={fetchedPost.content}  skipHtml={false} />
                                </div> 
                                {/* <div className="post_share">
                                    <div className="post-nam"> Share: </div>
                                    <div className="flex">
                                        <a href=".#"><i className="ti-facebook"></i>Facebook</a>
                                        <a href=".#"><i className="ti-linkedin"></i>Linkedin</a>
                                    </div>
                                </div> */}
                                <div className="post_tag d-flex">
                                    <div className="post-nam"> Tags: </div>
                                    <a href=".#">Wheels</a>
                                    <a href=".#">Saasland</a>
                                    <a href=".#">UX/Design</a>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-4">
                        {/* <Blogrightsidebar ServiceData={ServiceData}/> */}
                    </div>
                </div>
            </div>
        </section>
    )
}
export default BlogSingle;