import React, { Component } from "react";

class HomeBlogItems extends Component {
    render(){
        let ServiceData = this.props.ServiceData;
        return(
            <>
                {
                    ServiceData.Blist.map(post=>{ 
                        return(
                            <div className="col-lg-4 col-sm-6">
                                <div className="blog_list_item mb_50"  key={post.id}>
                                    <img className="img-fluid" src={require("../../img/" + post.image).default} alt=""/>
                                    <div className="blog_content">
                                        <div className="post_date">
                                            <h2>{post.pdate} <span>{post.Month}</span></h2>
                                        </div>
                                        <div className="entry_post_info">
                                            <a href="/blog"> By: Admin</a>
                                            <a href="/blog">SaasLand</a>
                                        </div>
                                        <a href="/blog-single">
                                            <h5 className="f_p f_size_20 f_500 t_color mb_20">{post.btitle}</h5>
                                        </a>
                                        <p className="f_400 mb_20">{post.bdescription}</p>
                                        <a href="/blog-single" className="learn_btn_two">Read More <i className="ti-arrow-right"></i></a>
                                    </div>
                                </div>
                            </div>
                        )
                    })
                }
            </>
        )
    }
}
export default HomeBlogItems;