import React from 'react';
import { Link }  from "react-router-dom"
import Markdown from "react-markdown"
import postlist from "../../posts.json"

const PostList = () => {
    const excerptList = postlist.map(post => {
        return post.content.split(" ").slice(0, 20).join(" ") + "..."
    })

    return (
        <div className="postlist row">
            {postlist.length && 
                postlist.map((post, i) => {
                    return (
                            <div className="blog_list_item mb_50 col-lg-6"  key={i}>
                            <img className="img-fluid" src={require(`../../img/${post.imglink}`).default} alt=""/>
                                <div className="blog_content">
                                    <div className="entry_post_info">
                                        <a href="/blog"> By: {post.author}</a>
                                        <a href="/blog">Published on {post.date}</a>
                                    </div>
                                    <a href="/blog-single">
                                        <h2 className="f_p f_size_20 f_500 t_color mb_20"><Link className="links" to={`/post/${post.id}`}>{post.title}</Link></h2>
                                    </a>
                                    <Markdown children={excerptList[i]} skipHtml={false} />
                                    <a href={`/post/${post.id}`} className="learn_btn_two">Read More <i className="ti-arrow-right"></i></a>
                                </div>
                            </div>
                    )
                })
            }
        </div>
    )
}
export default PostList;